import Vue from 'vue'
import { API_HOST, WEB_BASE_PATH } from './config'
var qs = require('qs')
const jsApiList = ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
export function requestShareSign () {
  Vue.$http.post('/index.php?a=signpackage_plandetail&m=index', qs.stringify({
    ultoken: userinfo.ultoken,
    uid: userinfo.uid,
    shareurl: window.location.href
  })).then(({ data }) => {
    settingShare(data)
  }, err => {
  })
}

export function settingShare (config) {
  wx.config({
    debug: false,
    appId: config.signPackage.appId,
    timestamp: config.signPackage.timestamp,
    nonceStr: config.signPackage.nonceStr,
    signature: config.signPackage.signature,
    jsApiList: jsApiList
  })
  let title = "我和家人都加入了众惠联互助，邀请你一起加入"
  let desc = "生病有钱治，费用大家摊，最高可获捐30万元"
  let link = window.location.href
  let imgUrl = API_HOST + WEB_BASE_PATH + "/images/helpin_logo.jpg"
  wx.ready(function () {
    jsApiList.forEach(n => {
      if (wx[n]) wx[n]({
        title: title,
        desc: desc,
        link: link,
        imgUrl: imgUrl
      })
    })
  })
}


