<template>
  <Popup :visible="visible" @on-close="$emit('on-close')">
    <form action="" v-on:submit.prevent="onSubmit">
      <div class="popup-backtext">
        <h3 class="popup-backtext-title">登录/注册</h3>
      </div>
      <div class="popup-form">
        <div class="popup-form-item" :class="{warn: form.phone.errMsg}">
          <input type="text" placeholder="请输入手机号" v-model="form.phone.value" @input="onInput(form.phone)">
          <div v-if="form.phone.errMsg" class="warn-info">{{form.phone.errMsg}}</div>
        </div>
        
        <div class="popup-form-item" :class="{warn: form.captcha.errMsg}">
          <div class="popup-form-item-col">
            <input type="text" placeholder="请输入右侧图形识别码" v-model="form.captcha.value" @input="onInput(form.captcha)">
            <div class="popup-form-item-extra" @click="refreshCaptcha"><img :src="captchaURL"/></div>
          </div>
          <div v-if="form.captcha.errMsg" class="warn-info">{{form.captcha.errMsg}}</div>
        </div>

        <div class="popup-form-item" :class="{warn: form.phoneCode.errMsg}">
          <div class="popup-form-item-col">
            <input type="text" placeholder="请输入手机验证码" v-model="form.phoneCode.value" @input="onInput(form.phoneCode)">
            <div class="popup-form-item-extra"><a href="javascript:;" @click="onSendMsgCode">
              {{enableCountdown ? (countdownDuration + 's') : '获取验证码'}}
              </a></div>
          </div>
          <div v-if="form.phoneCode.errMsg" class="warn-info">{{form.phoneCode.errMsg}}</div>
        </div>

      </div>
      <div class="popup-btn">
        <button href="javascript:;"><span><i v-if="isSubmit" class="rotaing"></i> {{isSubmit?'登录中':'登录'}}</span></button>
      </div>
    </form>
  </Popup>
</template>

<script>
import Popup from './popup'
import {validator} from '../mixins'
import {CAPTCHA_URL} from '../config'
let qs = require('qs')
let phone_reg = /^1[345789]\d{9}$/
let countdown = null // 倒计时标识
let countdownDuration = 90
export default {
  components: {
    Popup
  },
  mixins: [validator],
  props: {
    visible: false
  },
  watch: {
    visible (val) {
      if (val) {
        this.reset()
      }
    }
  },
  data () {
    return {
      captchaURL: CAPTCHA_URL,
      isSubmit: false,
      enableCountdown: false,
      countdownDuration: countdownDuration,
      form: {
        phone: {
          value: '',
          errMsg: '',
          roles: [
            { type: 'required', message: '请输入手机号', trigger: 'blur' },
            { type: 'regexp', pattern: phone_reg, message: '手机号格式不正确', trigger: 'blur' }
          ]
        },
        captcha: {
          value: '',
          errMsg: '',
          roles: [
            { type: 'required', message: '请输入图形识别码', trigger: 'blur' },
            { type: 'length',len: 4, message: '识别码格式不正确', trigger: 'blur' }
          ]
        },
        phoneCode: {
          value: '',
          errMsg: '',
          roles: [
            { type: 'required', message: '请输入手机验证码', trigger: 'change' },
            { type: 'length', len: 6, message: '验证码格式不正确', trigger: 'blur' }
          ]
        }
      }
    }
  },
  methods: {
    refreshCaptcha () {
      this.captchaURL = CAPTCHA_URL + '?n=' + Math.random()
    },
    reset () {
      for (var k in this.form) {
        this.form[k].errMsg = ''
      }
    },
    onSubmit () {
      if (this.isSubmit) return
      this.isSubmit = true
      this.validate(this.form, () => {
        window.userinfo = {}
        localStorage.setItem("userinfo", JSON.stringify(userinfo))
        this.$emit('on-ok')
        this.isSubmit = false
      }, () => {
        this.isSubmit = false
      })
    },
    onInput (field) {
      field.errMsg = ''
    },
    validatePhone () {
      if (!this.form.phone.value) {
        this.form.phone.errMsg = '请输入手机号'
        return false
      } else if (!phone_reg.test(this.form.phone.value)) {
        this.form.phone.errMsg = '手机号格式不正确'
        return false
      }
      return true
    },
    validateCaptcha () {
      if (!this.form.captcha.value) {
        this.form.captcha.errMsg = '请输入图形识别码'
        return false
      }
      return true
    },
    onSendMsgCode () {
      if (this.enableCountdown) return
      if (this.validatePhone() && this.validateCaptcha()) {
        this.sendMsgCode()
      }
    },
    sendMsgCode () {
      this.$http.post('/index.php?a=get_check_code&m=adduser', qs.stringify({
        phone: this.form.phone.value,
        simpleCaptcha: this.form.captcha.value
      })).then(({data}) => {
        if (data && Object.prototype.toString.call(data) === '[object Object]' && data.iswarn === 0) {
          this.enableCountdown = true
          this.countdownDuration = countdownDuration
          this.countdown()
        } else{
          this.form.captcha.errMsg = data.warn_info
        }
      }, err => {
      })
    },
    countdown () {
      clearTimeout(countdown)
      countdown = setTimeout(()=>{
        if (this.countdownDuration - 1 < 0) {
          this.enableCountdown = false
          // this.refreshCaptcha()
          return
        }
        this.countdownDuration--
        this.countdown()
      }, 1000)
    }
  },
  mounted () {
  }
}
</script>



