<template>
  <Popup :visible="visible" @on-close="$emit('on-close', $event)">
    <form action="" v-on:submit.prevent="onSubmit">
      <div class="popup-backtext">
        <h3 class="popup-backtext-title">填写凭证</h3>
        <p class="popup-backtext-text">请填写真实信息，如非真实信息 <br>互助计划将无法生效</p>
      </div>
      <div class="popup-form">
        <div class="popup-form-item" :class="{warn: form.name.errMsg}">
          <input type="text" placeholder="请输入姓名" v-model="form.name.value" @input="onInput(form.name)">
          <div v-if="form.name.errMsg" class="warn-info">{{form.name.errMsg}}</div>
        </div>

        <div class="popup-form-item" :class="{warn: form.idcard.errMsg}">
          <input type="text" placeholder="请输入身份证" v-model="form.idcard.value" @input="onInput(form.idcard)">
          <div v-if="form.idcard.errMsg" class="warn-info">{{form.idcard.errMsg}}</div>
        </div>
      </div>
      <div class="popup-names">
        <div class="popup-name" v-for="(item,index) in relations" :class="{active:index==checkedRelationIndex}" :key="index" @click="onChecked(index)">{{item.name}}</div>
      </div>
      <div class="popup-btn">
        <button href="javascript:;"><span><i v-if="isSubmit" class="rotaing"></i> {{isSubmit?'提交中':'确定'}}</span></button>
      </div>
    </form>
  </Popup>
</template>

<script>
import {relations} from '../config'
import Popup from './popup'
import {validator} from '../mixins'
var qs = require('qs')
export default {
  components: {
    Popup
  },
  mixins: [validator],
  props: {
    visible: false
  },
  watch: {
    visible (val) {
      if (val) {
        this.reset()
      }
    }
  },
  data () {
    return {
      relations: relations,
      checkedRelationIndex: 0,
      isSubmit: false,
      form: {
        name: {
          value: '',
          errMsg: '',
          roles: [
            { type: 'required', message: '请输入姓名', trigger: 'blur' }
          ]
        },
        idcard: {
          value: '',
          errMsg: '',
          roles: [
            { type: 'required', message: '请输入身份证', trigger: 'blur' },
            { type: 'idcard', message: '身份证格式不正确', trigger: 'blur' }
          ]
        },
        phoneCode: {
          value: ''
        }
      }
    }
  },
  methods: {
    reset () {
      for (var k in this.form) {
        this.form[k].errMsg = ''
      }
    },
    onChecked (index) {
      this.checkedRelationIndex = index
    },
    onSubmit () {
      this.validate(this.form, () => {
        this.submit()
      })
    },
    onInput (field) {
      field.errMsg = ''
    },
    submit () {
      if (this.isSubmit) return
      this.isSubmit = true
      this.$http.post('/index.php?a=dojoin&m=index', qs.stringify({
        ultoken: userinfo.ultoken,
        uid: userinfo.uid,
        plan_id: 8,
        insurant_name: this.form.name.value,
        insurant_idcard: this.form.idcard.value,
        insurant_relation: this.relations[this.checkedRelationIndex].rId,
        pay_num: 10,
        phone_num: userinfo.phone,
        voucher_id: 0
      })).then(({data}) => {
        this.isSubmit = false
        if (!data || Object.prototype.toString.call(data) !== '[object Object]') return
        this.$emit('on-ok', data)
      }, err => {
        this.isSubmit = false
      })
    }
  }
}
</script>



