<template>
  <div id="app">
    <div class="banner">
      <img class="wide-img" src="./assets/banner.png" />
    </div>

    <div class="main-wrp">
      <div class="main">
        <div class="title">
          <img class="wide-img" src="./assets/teletext-1.png" />
          <ul class="list" v-if="data.length">
            <li v-for="(item, index) in (visibleAll ? data : data.slice(0, visibleLimit))" :key="index" @click="onChecked(index,item)">
              <div class="name">{{item.insurant_name}}</div>
              <div class="sum">余额：
                <span>{{item.balance}}</span>元</div>
              <div class="handle">
                <transition name="checkbox">
                  <a class="checkbox unchecked" key="unchecked" v-if="index!=checkedCertIndex" href="javascript:;"></a>
                  <a class="checkbox checked" key="checkbox" v-if="index==checkedCertIndex" href="javascript:;"></a>
                </transition>
              </div>
            </li>
            <li class="list-more" @click="showMore" v-if="data.length>visibleLimit">
              <a href="javascript:;">{{visibleAll?'收 起':'显示更多'}}</a>
            </li>
          </ul>
          <div class="list-null" v-else>您还没加入互助，快去加入吧！</div>
        </div>
        <a class="btn plain" @click="onAddCert">
          <i class="icon icon-join"></i>
          <span>加入互助</span>
        </a>
      </div>
    </div>

    <a class="btn draw" @click="onTopay">每人5元 立即领取</a>

    <div class="teletext-box">
      <div class="teletext">
        <img class="wide-img" src="./assets/teletext-2.png" />
      </div>
      <div class="teletext">
        <img class="wide-img" src="./assets/teletext-3.png" />
      </div>
      <div class="teletext">
        <img class="wide-img" src="./assets/teletext-4.png" />
      </div>
      <div class="example">
        举例：
        <div class="example-text">
          郭先生17年12月份充值领取保障，半年后确诊肝癌，向众惠联互助申请互助金保障。10万众惠联互助会员，每人捐助3元组成30万互助金，郭先生获得30万互助金及时用于治疗，病情得到控制。
        </div>
      </div>
      <Faqs/>
    </div>

    <div class="contact">
      更多疑问可咨询客服:
      <a href="tel:010-56610578">010-56610578</a>
    </div>

    <Cert :visible="visibleCertPopup" @on-ok="onAddCertCallback" @on-close="visibleCertPopup = false" />
    <Login :visible="visibleLoginPopup" @on-ok="onLoginCallback" @on-close="visibleLoginPopup = false"/>
    <transition name="bounce">
      <Pay v-if="visiblePayPopup" />
    </transition>
  </div>
</template>

<script>
import Faqs from './components/faqs'
import Popup from './components/popup'
import Login from './components/login'
import Cert from './components/cert'
import Pay from './components/pay'
import {requestShareSign} from './share'
var qs = require('qs')
import { API_HOST, API_BASE_PATH } from './config'
window.userinfo = JSON.parse(localStorage.getItem("userinfo"))
userinfo = {ultoken: 'a455dcbeced8518ff30d2091b7e1db64', uid: '1'}
export default {
  name: 'App',
  components: { Faqs, Popup, Login, Cert, Pay },
  data () {
    return {
      data: [],
      visibleLimit: 5,
      visibleAll: false,
      checkedCertIndex: 0,
      visibleCertPopup: false,
      visibleLoginPopup: false,
      visiblePayPopup: false,
      isLogin: false
    }
  },
  methods: {
    showMore () {
      this.visibleAll = !this.visibleAll
    },
    onChecked (index, item) {
      this.checkedCertIndex = index
    },
    onTopay () {
      if (this.isLogin) {
        if (!this.data.length) return
        let cert_id = this.data[this.checkedCertIndex].cert_id
        this.$http.post('/index.php?a=dorecharge&m=index', qs.stringify({
          ultoken: userinfo.ultoken,
          uid: userinfo.uid,
          cert_id: cert_id,
          pay_num: 10,
          voucher_id: 0
        })).then(({ data }) => {
          if (data.iswarn < 1) {
            if (data.out_trade_no >= 1) {
              let to_pay_uri = `${API_HOST}${API_BASE_PATH}/wxpay/example/jsapi.php?out_trade_no=${data.out_trade_no}&total_fee=${data.total_fee}`
              window.location.href = to_pay_uri
            }
          }
        }, err => {
          this.$toast.error('请求·dorecharge·失败!', '错误')
        })
      } else {
        this.visibleLoginPopup = true
      }
    },
    onAddCert () {
      this.isLogin ? this.visibleCertPopup = true : this.visibleLoginPopup = true
    },
    onLoginCallback () {
      this.visibleLoginPopup = false
      this.requestPlanList()
    },
    onAddCertCallback () {
      this.visibleCertPopup = false
      this.requestPlanList()
    },
    login () {
      this.$http.post('/index.php?a=dologin&m=index', qs.stringify({
        phone1: '18611852267',
        password1: '110220'
      })).then(({ data }) => {
        userinfo = data
        localStorage.setItem("userinfo", JSON.stringify(userinfo))
        this.requestUserinfo()
      }, err => {
      })
    },
    /**
     * 请求当前用户信息
     */
    requestUserinfo () {
      if (!userinfo || !userinfo.ultoken || !userinfo.uid) return
      this.$http.post('/index.php?a=islogin&m=index', qs.stringify({
        ultoken: userinfo.ultoken,
        uid: userinfo.uid
      })).then(({ data }) => {
        if (data && Object.prototype.toString.call(data) === '[object Object]' && data.uid) {
          this.isLogin = true
          userinfo = data
          localStorage.setItem("userinfo", JSON.stringify(userinfo))
          this.requestPlanList()
          requestShareSign()
        }
      }, err => {
        localStorage.setItem("userinfo", JSON.stringify({}))
      })
    },
    /**
     * 请求计划列表
     */
    requestPlanList () {
      this.$http.post('/index.php?a=myplan&m=index', qs.stringify({
        ultoken: userinfo.ultoken,
        uid: userinfo.uid
      })).then(({ data }) => {
        if (!data.plans) return
        if (Object.prototype.toString.call(data.plans) !== '[object Array]') return
        this.data = data.plans
        this.checkedCertIndex = 0
      }, err => {
        this.$toast.error('加入计划失败!', '错误')
      })
    }
  },
  mounted () {
    this.requestUserinfo()
  }
}
</script>



