<template>
<transition name="fade">
  <div class="popup" v-if="visible">
    <div class="popup-content" :class="{active:visibleContent}">
      <a class="popup-close" :class="{active:visibleClose}" href="javascript:;" @click="handleClose"></a>
      <slot/>
    </div>
  </div>
</transition>
</template>

<script>
export default {
  props: {
    visible: false
  },
  data () {
    return {
      visibleClose: false,
      visibleContent: false,
    }
  },
  watch: {
    visible (val) {
      setTimeout(()=>{
        this.visibleContent = val
      }, 10)
      setTimeout(()=>{
        this.visibleClose = val
      }, 200)
    }
  },
  methods: {
    handleClose () {
      this.visibleClose = false
      setTimeout(()=>{
        this.visibleContent = false
      }, 100)
      setTimeout(()=>{
        this.$emit('on-close')
      }, 200)
    }
  }
}
</script>



