<template>
  <div class="pay-wrp">
    <div class="pay" :class="payStatus">
      <div class="pay-icon"></div>
      <div class="money">￥10</div>
      <div v-if="payStatus=='success'">
        <p>支付成功</p>
        <div class="topay">完成</div>
      </div>
      <div v-else-if="payStatus=='error'">
        <p>支付失败</p>
        <div class="topay">重新支付</div>
      </div>
      <div v-else>
        <p>总金额10元，优惠券抵5元，需充值5元</p>
        <div class="topay" @click="onToPay">确认支付</div>
      </div>
      <div class="footer">愿我们都会被这个世界温柔以待</div>
    </div>
  </div>
</template>

<script>
var qs = require('qs')
import {API_HOST, API_BASE_PATH} from '../config'
export default {
  data () {
    return {
      payStatus: '' // success,error
    }
  },
  methods: {
    onToPay () {
      this.$http.post('/index.php?a=dorecharge&m=index', qs.stringify({
        ultoken: userinfo.ultoken,
        uid: userinfo.uid,
        cert_id: userinfo.cert_id,
        pay_num: 10,
        voucher_id: 0
      })).then(({data}) => {
        if(data.iswarn < 1){
          if(data.out_trade_no >= 1){
            let to_pay_uri = API_HOST + API_BASE_PATH + "/wxpay/example/jsapi.php?out_trade_no=" + data.out_trade_no +  "&total_fee=" + data.total_fee
            window.location.href = to_pay_uri
            // window.open(to_pay_uri)
          }
        }
      }, err => {
      })
    }
  }
}
</script>
<style>
.pay-wrp{
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: RGBA(248, 247, 247, 1.00)
}
.pay{
  background: #FFF;
  border-radius: 20px;
  width: 90%;
  margin: 30% auto 0;
  position: relative;
  box-shadow: 0 0 4px 2px RGBA(0, 0, 0, 0.3);
  text-align: center;
}
.pay-icon {
  background: url(../assets/icon-pay.png) center no-repeat/100%;
  position: absolute;
  left: 50%;
  top: -50px;
  width: 100px;
  height: 100px;
  margin-left: -50px;
  border-radius: 50%;
}
.money {
  color: RGBA(91, 91, 91, 1.00);
  font-size: 5rem;
  padding: 25% 0 5%;
}
.topay{
  display: block;
  font-size: 1.2rem;
  line-height: 1;
  color: #FFF;
  background: RGBA(46, 188, 131, 1.00);
  border-radius: 10px;
  padding: 15px 0;
  margin: 10% 50px 10%;
  font-size: 20px;
}
.footer{
  border-top: 2px dotted RGBA(46, 188, 131, 1.00);
  position: relative;
  padding: 12% 0;
  color: #888;
  line-height: 1;
  font-size: 14px;
}
.footer::before,.footer::after{
  /* content: ''; */
  position: absolute;
  top: -8px;
  width: 15px;
  height: 15px;
  background: RGBA(46, 188, 131, 1.00);
  border-radius: 50%;
}
.footer::before{
  left: -8px;
}
.footer::after{
  right: -8px;
}
.pay.success .pay-icon{
  background-image: url(../assets/icon-pay-success.png);
}
.pay.success .topay{
  background: RGBA(255, 162, 4, 1.00);
}
.pay.success .footer{
  border-top-color: RGBA(255, 162, 4, 1.00);
}

.pay.error .pay-icon{
  background-image: url(../assets/icon-pay-error.png);
}
.pay.error .topay{
  background: RGBA(255, 83, 82, 1.00);
}
.pay.error .footer{
  border-top-color: RGBA(255, 83, 82, 1.00);
}
</style>



