<template>
  <div class="faqs">
    <div class="item" v-for="(item, index) in faqs" :key="index">
      <div class="issue">{{(index + 1) + '. ' + item.issue}}</div>
      <div class="reply">{{item.reply}}</div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      faqs: [{
        issue: '什么是”30万大病互助金保障”？',
        reply: '30万大病互助金保障是众惠联互助会员专享保障，领取保障后，将来如患大病，可申请最高30万元互助金。'
      }, {
        issue: '为什么要充值才能领取保障？',
        reply: '充值的小额互助金是用于帮助大病的会员。每人预存一点钱，放在一起，以后谁生病了就从这些钱里面出医疗费。这样就可以”每个人平时花很少的钱帮别人，自己生病也能得到救命钱”。'
      }, {
        issue: '互助的保障范围是什么？',
        reply: '互助的保障范围是癌症，白血病,尿毒症等《111种常见重大疾病，以及意外伤残、身故。'
      }]
    }
  }
}
</script>
<style>
.faqs{
  margin-top: 15px;
}
.faqs .item{
  border-bottom: 1px solid #eee;
  padding-bottom: 16px;
  margin-bottom: 16px;
}
.faqs .item .issue{
  color: #56bdf8;
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 700;
}
.faqs .item .reply{
  background: #f6f6f6;
  padding: 20px;
  line-height: 1.8;
}
</style>



