/**
 * 表单验证mixins
 */
import {checkCertificateNo} from './utils'
export const validator = {
  methods: {
    validate (form, success, error) {
      let isNext = true
      for (let k in form) {
        let field = form[k]
        field.errMsg = ''
        if (!field.roles || !field.roles.length) continue
        field.roles.forEach(n => {
          if (field.errMsg) return
          switch (n.type) {
            case 'required':
              if (!field.value) {
                field.errMsg = n.message
                isNext = false
              }
              break
            case 'length':
              if (field.value.length !== n.len){
                field.errMsg = n.message
                isNext = false
              }
              break
            case 'regexp':
              if (!new RegExp(n.pattern).test(field.value)) {
                field.errMsg = n.message
                isNext = false
              }
              break
            case 'idcard':
              if (!checkCertificateNo(field.value)) {
                field.errMsg = n.message
                isNext = false
              }
              break
          }
        })
      }
      if (isNext && success) success()
      else if (!isNext && error) error()
    }
  }
}



